import { Component } from "react";

import fetchJokes from "./../../services/apiRequest";
import Joke from "../Joke/Joke";
import "./DadJokes.css";

class DadJokes extends Component {
  static defaultProps = {
    numJokesToGet: 10,
  };

  constructor(props) {
    super(props);

    this.state = {
      jokes: [],
    };
  }

  componentDidMount() {
    const jokes = localStorage.getItem("jokes");
    const parsedJokes = JSON.parse(jokes);

    if (parsedJokes) {
      this.setState({ jokes: parsedJokes });
    } else {
      this.addMoreJokes();
    }
  }

  componentDidUpdate(prevProps, prevState) {
    const nextJokes = this.state.jokes;
    const prevJokes = prevState.jokes;

    if (nextJokes !== prevJokes) {
      localStorage.setItem("jokes", JSON.stringify(nextJokes));
    }
    this.sortJokes();
  }
  Vote = (joke) => {
    const { jokes } = this.state;

    const newJokes = jokes.map((j) => {
      if (j.id === joke.id) {
        return joke;
      } else {
        return j;
      }
    });

    this.setState({ jokes: newJokes });
  };

  addMoreJokes = async () => {
    localStorage.setItem("jokes", []);
    const allJokes = [];

    while (allJokes.length !== this.props.numJokesToGet) {
      const newJoke = await fetchJokes();
      newJoke.rating = 0;
      allJokes.push(newJoke);
    }

    this.setState({
      jokes: [...allJokes],
    });
  };

  sortJokes = () => {
    const { jokes } = this.state;
    const sorted = jokes.sort((f, s) => {
      return s.rating - f.rating;
    });
    return sorted;
  };

  render() {
    const sortedJokes = this.sortJokes();

    return (
      <div className="container">
        <button className="more" onClick={this.addMoreJokes}>
          ðŸ¤£
        </button>
        <ul>
          {sortedJokes.map((joke) => {
            return (
              <li key={joke.id}>
                <Joke
                  text={joke.joke}
                  rating={joke.rating}
                  id={joke.id}
                  upVote={this.Vote}
                />
              </li>
            );
          })}
        </ul>
      </div>
    );
  }
}

export default DadJokes;
